@import url(http://fonts.googleapis.com/css?family=Lalezar);

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: local('Open Sans Regular'), local('OpenSans-Regular'), url(https://fonts.gstatic.com/s/opensans/v15/mem8YaGs126MiZpBA-UFVZ0d.woff) format('woff');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  src: local('Open Sans Bold'), local('OpenSans-Bold'), url(https://fonts.gstatic.com/s/opensans/v15/mem5YaGs126MiZpBA-UN7rgOUuhv.woff) format('woff');
}

@font-face {
  font-family: 'Lalezar';
  font-style: normal;
  font-weight: 400;
  src: local('Lalezar'), local('Lalezar-Regular'), url(https://fonts.gstatic.com/s/lalezar/v3/zrfl0HLVx-HwTP82Yaf4JQ.woff) format('woff');
}

body {
  margin: 0;
  padding: 0;
}

body, input, select {
  font-family: 'Open Sans','Exo 2', 'Arial', 'Microsoft Yahei', 'Ezra Sil', 'Ezra Sil SR', 'CODE2000', sans-serif;
}

