.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/****************** MINE ***********/

.forms-rtl {
  direction: rtl;
}

.form {
  display: inline-block;
}

.field {
  /*padding: 8px;*/
  text-align: left;
  line-height: 32px;
  margin-bottom: 6px;
  width: 500px;
}

.forms-rtl .field {
  text-align: right;
}

.field input,
.field select
{
  padding-left: 8px;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 16px;
  height: 42px;
  /* -webkit-appearance: none; */
  /* border: 1px solid #888; */
}

.field input[type='text'],
.field input[type='tel']
{
  padding-right: 8px;
}

.field input.date-text {
  width: 60px;
  margin-right: 8px;
}

.field input.date-text-year {
  width: 90px;
  margin-right: 8px;
}

.field select.date-select {
  width: auto;
  margin-right: 8px;
}

.field input.invalid {
  border-color: red;
  __border: 1px solid #c06104;
  background-color: #fff8e0;
}

.field select
{
  -moz-appearance: none;
  -webkit-appearance: none;
}

select {
  width: 100%;
  padding-left: 8px;
  padding-right: 32px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  __padding: .5em;
  background: #f0f0f0;
  border: 1px solid #c0c0c0;
  __border-radius: 3px;
  __padding: 1em 2em 1em 1em;
  __font-size: 1em;
}
.select-container { position:relative; display: inline; }
.select-container:after {
  content:""; width:0; height:0; position:absolute; pointer-events: none;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  top: 7px;
  right: 12px;
  border-top: 8px solid black;
  opacity: 0.5;
}

select::-ms-expand {   /* https://clicknathan.com/web-design/styling-select-elements-crossbrowser/ */
  display: none;
}

.radio-fieldset {
  margin: 16px 0 0 0;
  border: 1px solid #c0c0c0;
  border-radius: 4px;
  background-color: #f8f8f8;
}

.radio-fieldset legend {
  background-color: #666;
  border-radius: 4px;
  padding: 6px 12px;
  color: #fff;
  font-size: 14px;
  line-height: 20px;
}

.radio-fieldset input {
  height: 22px;
  width: 22px;
  position: absolute;
  left: 0;
  top: 2px;
}

.radio-fieldset label {
  position: relative;
  display: inline-block;
  padding-left: 36px;
  padding-top: 4px;
  padding-bottom: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
}

.radio-fieldset label:hover {
  color: #000;
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  margin: -1px;
}

.radio-fieldset span {
  line-height: 22px;
}

.forms-rtl .radio-fieldset label {
  padding-left: 0;
  padding-right: 36px;
}

.forms-rtl .radio-fieldset input {
  right: 0;
}


/*********************** ***********************/

.field input:hover,
.field select:hover {
  /* border: 1px solid #0b0aff; padding: 9px; */
  border-color: #0b0aff;
}

.field-label { display: block; width: 100%; font-size: 15px; }
.field span { display: block; width: 100%; color: #333; font-size: 14px; }

.invalid input,
.invalid select {
  border: 1px solid #c06104;
  background-color: #fff8e0;
}

select:focus::-ms-value {
  background: none; /* remove blue background on ie10/ie11 when selected*/
  color: #000;
}

.empty {
  color: #a0a0a0;
}

select .empty:focus {
  color: #a0a0a0;
}

option {
  color: #000;
}

option:disabled /* .empty, */
{
  color: #808080;
  background-color: #d0d0d0;
}

/***** CHECKBOX *****/

.checkbox input {
  height: 22px;
  width: 22px;
  position: absolute;
  margin: 8px 6px;
  pointer-events: none;
}

.checkbox label {
  padding: 7px 5px 6px 36px;
  line-height: 22px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #fff;
  border-radius: 3px;
  display: block;
  width: 100%;
}

.checkbox input[type=checkbox]:checked + label {
  color: #4d494d;
  font-weight: 700;
}

.checkbox label:hover
{
  color: #000030 !important;
  background-color: #fbfbfb;
  border: 1px solid #d2d8e6;
}

/*.checkbox input[type=checkbox]:focus + label
{
  color: #000070 !important;
  border: 1px solid #d2d8e6;
}*/

.forms-rtl .checkbox label {
  padding: 7px 36px 6px 5px;
}

/***** CHINESE FONTS *******/

.CHINA .field-label,
.CHINA .radio-fieldset label,
.CHINA .checkbox label {
  font-size: 18px;
}

.CHINA .radio-fieldset legend {
  font-size: 20px;
  font-weight: bold;
}

/***** MEDIUM SIZE *******/

.LANG_MEDIUM .field-label,
.LANG_MEDIUM .radio-fieldset label,
.LANG_MEDIUM .checkbox label {
  font-size: 18px;
  color: #444;
}

/***** LARGE SIZE *******/

.LANG_LARGE .field-label {
  font-family: 'Lalezar', cursive, sans-serif;
  font-size: 22px;
  font-weight: normal;
  color: #576449;
  /*margin-bottom: 2px;*/
}

.LANG_LARGE .radio-fieldset legend {
  font-size: 22px;
}

.LANG_LARGE .field input,
.LANG_LARGE .field select {
  font-size: 22px;
}

/***** RESPONSIVE *******/

@media screen and (max-width: 520px) {
  .form { display: block; margin: 0 12px; }
  .field { width: 100%; }
}



